let base = '/single-page-web-app/'
if (process.env.NODE_ENV === 'development') base = '/'

export default (dispatch, {match, location, history}) => {
  return {
    fetchMarkdown: () => {
      let pathname = location.pathname
      if (!pathname.endsWith('/')) pathname += '/'
      pathname = pathname.replace(base, '')
      let file = `${base}docs${pathname}/`
      if (file === `${base}docs/`) file = `${base}docs/introduction`
      if (location.search.includes('exercises')) file += '-exercises'
      file += '.md'

      dispatch({
        type: '@@spwa/FETCHING',
        payload: {markdown: 'Fetching, please wait ...'}
      })

      fetch(file)
        .then(res => res.text())
        .then(markdown => {
          if (markdown.includes('<!DOCTYPE html>')) {
            throw new Error(`Could not find file \`${file}\``)
          } else {
            dispatch({
              type: '@@spwa/FETCHED',
              payload: {markdown}
            })
          }
        })
        .catch((err) => {
          dispatch({
            type: '@@spwa/FETCHED_ERROR',
            payload: {markdown: err.message}
          })
        })
    }
  }
}



